.header, .footer {
    display: flex;
    justify-content: space-evenly;
    width: 100vw;
    background:  var(--color-white) 0% 0%;
    box-shadow: 0px 3px 6px #00000029;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1352px;
    max-width: 80%;
    height: 70px;
}

.logo_text {
    text-align: left;
    line-height: 37;
    font-size: 32px;
    line-height: 37px;
    font-family: var(--font-raleway);
    font-weight: 300;
    color: var(--color-primary);
}

.nav_list {
    display: flex;
    text-align: center;

}

.nav_link {
    display: flex;
    letter-spacing: 3.28px;
    color:  var(--color-black);
    font-size: 11px;
    line-height: 15px;
    font-family: var(--font-open-sans);
    font-weight: bold;
    color:  var(--color-grey);
    margin-left: 95px;
    text-decoration: none;
    height: 66px;
    align-items: center;
    border-bottom-color:  var(--color-white);
    border-bottom-style: solid;
    border-bottom-width: 4px;
}

.nav_link:hover {
    display: flex;
    text-align: center;
    font: normal normal bold 11px/15px var(--font-open-sans);
    letter-spacing: 3.28px;
    color:  var(--color-black);
    border-bottom-color: rgb(8, 166, 228);
    border-bottom-style: solid;
    border-bottom-width: 4px;
    height: 66px;
}
